import {
	ADD_TASK,
	COMPLETE_TASK,
	DELETE_TASK
} from '../constants'

const task = (state, action) => {
	switch (action.type) {
		case (ADD_TASK):
			return {
				id: (new Date) + Math.random(),
				completed: false,
				title: action.payload.title,
				text: action.payload.text,
				tags: action.payload.tags,
				dateToDo: action.dateToDo,
				date: new Date(),
			}

		case (COMPLETE_TASK):
			//   ,      
			const currentTask = state.find((item) => {
				return item.id == action.payload
			})
			return Object.assign({}, currentTask, {
				completed: !currentTask.completed
			})
		default:
			return state
	}
}

const tasks = (state = [], action) => {
	switch (action.type) {
		case (ADD_TASK):
			return [...state, task(state, action)]
		case (COMPLETE_TASK):
			//   ,         
			const completedTask = task(state, action)
			const newState = state.reduce((prev, item) => {
				if (completedTask.id == item.id) {
					return prev.concat(completedTask)
				} else {
					return prev.concat(item)
				}
			}, [])
			return newState

		case (DELETE_TASK):
			return state.reduce((prev, item) => {
				if (action.payload !== item.id) {
					return prev.concat(item)
				} else {
					return prev
				}
			}, [])
		default:
			return state
	}
}

export default tasks


